<template>
<div class="row" v-if="ready">
    <!--right-->
    <div data-app class="col-12">
        <label>User Group Management</label>
            <div class="form-group">
                <input type="text" class="form-control" v-model="search_user_key" @keyup="search_user" style='margin-bottom:1rem' placeholder="Search User"/>
                <div v-if="hv_user" style='margin-bottom:1rem'>
                    <v-data-table
                    :headers="headers_list"
                    :items="userdata"
                    :search="search"
                    class="elevation-1"
                    :rows-per-page-items="10"                  
                    >
                    <template v-slot:items="props">
                        <tr style='cursor:pointer' @click="add_user(props.item.username)"> 
                            <td>Add</td>
                            <td>{{ props.item.name }}</td>
                            <td>{{ props.item.username }}</td>   
                        </tr>
                    <template v-slot:pageText="props">
                        Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                    </template>
                    
                    </template>
                    </v-data-table> 
                </div>    
            <!--live search-->           
                <div class='flex col-12 px0'>
                    <div class="col-4 px0">
                        <select v-model="dept" class="form-control select" v-if="dept_ready" @change="new_list">
                            <option v-for="dep in dept_list" :value="dep.dept_id">{{ dep.full_dept }}</option>
                        </select>
                    </div>
                    <div class="col-4">
                        <select v-model="subdept" class="form-control select" v-if="dept_ready" @change="fetch_sub_list">
                            <option :value="dept">N/A</option>
                            <option v-for="sdep in subdept_list" :value="sdep.subdept">{{ sdep.subdept_full}}</option>
                        </select>
                    </div>
                    <div class="col-2"></div>
                    <div class="col-2 px0">
                        <select v-model="title" class="form-control select" :disabled="dept!=subdept">
                            <option v-for="level in title_dep">{{ level }}</option>
                        </select>
                    </div>             
                </div>
            </div>           
            
            <v-data-table
                :headers="headers"
                :items="userlist"
                class="elevation-1"
                :rows-per-page-items="10"
            >
            <template v-slot:items="props">
                <tr style='cursor:pointer' @click="remove_user(props.item)">
                    <td>Remove</td>
                    <td>{{ props.item.name }}</td>
                    <td>{{ props.item.username }}</td> 
                    <td>{{ props.item.subdept }}</td> 
                    <td>{{ props.item.department_title }}</td> 
                </tr>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>        
            </template>
            </v-data-table><br/>
            <!--member-->
    </div>
</template>
<script>
export default {
    data(){
        return{
            ready:false,
            subdept_list:[],
            subdept:'',
            title:'Admin',
            title_dep:['superadmin','admin','Manager','Principal','Principal Secretary','PTA','Department Head','Committee Head','Secretary','Member'],
            search_user_key:"",
            userlist:[],
            dept_list:[],
            dept_list_name:[],
            userdata:[],
            dept_ready:false,
            dept:'IT & Resource Committee',
            title:"Member",
            userlist_ready:false,
            hv_user:false,
            headers: [
                { text: 'Action',value :'action',sortable: false},
                { text: 'Name',value:'name',sortable: false},
                { text: 'Username',value:'username',sortable: false},
                { text: 'Sub-Department',value:'subdept',sortable: false},
                { text: 'Title',value:'department_title',sortable: false},
            ], 
            headers_list: [
                { text: 'Action',value :'action',sortable: false},
                { text: 'Name',value:'name',sortable: false},
                { text: 'Username',value:'username',sortable: false},
            ],
            user_check:'',
        }
    },
async created(){
    await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                this.user_check = response.data[0].signed;
                var perm = response.data[0];
                if(!(this.user_check=='set')){
                    this.$router.push('../user_setup');
                }
                if(perm.proc_level<4){
                    this.$alert("No permission").then(
                        ()=>{
                            this.$router.go(-1);
                        }
                    )
                }
            }
        )
    await axios.get('Procurement/shared_backend/alldept.php')
    .then(
        (response)=>{
            this.dept_list = response.data;
            this.dept = response.data[0].dept_id;
            this.dept_ready = true;
        }
    )
    var formData = new FormData();
    var dept = this.dept;
    formData.append('dept', dept);
    await axios.post('Procurement/admin/backend/userlist.php',formData)
    .then(
        (response)=>{
            if(response.data!='empty'){
                this.userlist = response.data;
            }
        }
    )
    await this.fetch_subdept();
    this.ready=true;
},
    methods:{
        add_user(targetID){
            var formData = new FormData();
            var dept = this.dept;
            var title = this.title;
            formData.append('username', targetID);
            formData.append('dept', dept);
            formData.append('subdept',this.subdept);
            formData.append('title', title);
            axios.post('Procurement/admin/backend/add_member.php',formData)
            .then(
                (response)=>{
                    this.new_list();
                    this.hv_user=false; 
                    this.search_user_key="";
                }); 
        },
        search_user(){
            var formData = new FormData();
            formData.append('key', this.search_user_key);
            axios.post('RequestPortal/backend/search_user_list.php',formData)
            .then(
                (response)=>{
                    if(response.data == "Empty"){
                        this.userdata = [];
                       this.hv_user=false; 
                    } else {
                        this.userdata = response.data;
                        this.hv_user=true;
                    }
                }); 
        },
        fetch_sub_list(){
            if(this.subdept==this.dept){
                this.new_list();
            } else {
                this.title = 'Member';
                var formData = new FormData();
                formData.append('maindept',this.dept);
                formData.append('subdept',this.subdept);
                axios.post('Procurement/admin/backend/subdept_member.php',formData)
                    .then(
                        (response)=>{
                            if(response.data=='empty'){
                                this.userlist=[];
                            } else {
                                this.userlist = response.data;
                            }
                        }
                    )
            }
        },
        new_list(){
            var formData = new FormData();
            var dept = this.dept;
            this.subdept = dept;
            formData.append('dept', dept);
            axios.post('Procurement/admin/backend/userlist.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='empty'){
                            this.userlist = [];
                        } else {
                            this.userlist = response.data;
                        }                  
                        this.fetch_subdept();
                    }
                ) 
        },
        fetch_subdept(){
            var formData = new FormData();
            formData.append('maindept',this.dept);
            axios.post("Procurement/shared_backend/fetch_sub_dept.php",formData)
                .then(
                    (response)=>{
                        if(response.data=='empty'){
                            this.subdept_list = [];
                            this.subdept = this.dept;
                        } else {
                            this.subdept_list = response.data;
                            this.subdept = this.dept;
                        }
                    }
                )

        },
        remove_user(target){
            var formData = new FormData();
            formData.append('username', target.username);
            formData.append('dept', this.dept);
            formData.append('subdept',target.subdept);
            axios.post('Procurement/admin/backend/delete_user.php',formData)
            .then(
                (response)=>{
                    this.new_list();
                }
            )
        }
    }
}
</script>
<style scoped>
    .click_row{
        cursor:pointer;
    }
    .flex{
        display:flex;
        flex-direction: row;
    }
    .px0{
        padding-left:0;
        padding-right:0;
    }
</style>


